	module.exports = {"data":{"post":{"html":"<p>I've mentioned <em>Pfingstsingen</em> a few times here, when (separate) groups of boys and girls go house to house on Pentecost Sunday and sing, expecting sweets or some money in return. The tradition is kept alive here in L端llau... while we've heard of young men bringing a <em>Pfingstbaum</em> in other towns, but nowhere else with kids.</p>\n<p>Since Christopher is now in school, he gets to participate this year (in fact, he's an honorary door ringer... I think that's called the <em>Pfingstbock</em> or <em>Pfingstpfost</em>). They've practiced 4 or 5 times and today decorated a wagon where they can deposit their loot. They're also packing water pistols, in case they happen to cross paths with the girls' group.</p>\n<p>He's also had to learn the song <a href=\"http://ingeb.org/Lieder/bollerei.html\"><em>\"Bolle reiste j端ngst zu Pfingsten\"</em></a>, an old children's song. I never paid much attention to the <a href=\"http://de.wikipedia.org/wiki/Bolle_reiste_j%C3%BCngst_zu_Pfingsten\">lyrics</a>. It's in dialect, but I found a <a href=\"http://jetzt.sueddeutsche.de/texte/anzeigen/322343\">modern explanation</a>. Let's just say that it would never pass the censors these days... child neglect, alcohol abuse, knife fights, domestic violence, and finally suicide. Tipper Gore would have field day.</p>","fields":{"title":"Bolle took a trip for Pentecost","date":"26 May 2007","url":"https://papascott.de/2007-05-26-bolle-took-a-trip-for-pentecost/"}}},"pathContext":{"slug":"/2007-05-26-bolle-took-a-trip-for-pentecost/","prev":{"fields":{"title":"New Look","date":"25 May 2007","slug":"/2007-05-25-new-look/"}},"next":{"fields":{"title":"","date":"26 May 2007","slug":"/2007-05-26-it-was-thirty-years-ago/"}}}}

